body {
  padding: 0;
  margin: 0;
  font-family: 'Comfortaa', cursive;
}

.bg-grey {
  background-color: rgb(238, 237, 237);
}

.fs-1 {
  font-size: 2.3vh;
}

.fs-2 {
  font-size: 1.8vh;
}

.fs-3 {
  font-size: 1.9vh;
}

.fs-4 {
  font-size: 2vh;
}

.fs-3 a {
  text-decoration: none;
}
.fs-3:hover {
  color: white !important;
}

.bg-black {
  background-color: #222;
}

.btn-primary {
  background: #57dddf;
  border-color: #57dddf;
  color: black;
  transition: ease-out 0.3s;
}

.btn-primary:hover {
  background: none;
  color: black;
  border-color: #57dddf;
}

textarea {
  resize: none;
}

.wc {
  white-space: normal;
  word-break: break-all;
}

.sidebar {
  height: 100vh;
}

.facebook {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  font-size: 25px;
  color: white;
  transition: ease-out 0.5s;
}

.google {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  font-size: 25px;
  color: white;
  transition: ease-out 0.5s;
}

.google:hover {
  color: #c5031a;
}

.facebook:hover {
  color: #2c3bd8;
}

.img-profile {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.img-profile p {
  align-items: center;
  font-size: 2.9vh;
  font-weight: bold;
  display: flex;
  justify-content: center;
  margin-top: 12px;
}

.button {
  border-radius: 5px;
  border-color: rgba(0, 0, 0, 0);
  font-weight: 500;
  transition: ease-out 0.5s;
}

.button:hover {
  border-color: #57dddf;
  color: white;
  background-color: rgba(0, 0, 0, 0);
}

.bg-lightaqua {
  background-color: #57dddf;
  border-color: #57dddf;
}

.td {
  text-decoration: none !important;
}

.text-indent {
  text-indent: 30px;
}

.carousel-indicators .active {
  background-color: #57dddf;
}

.spinner {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: 1px) and (max-width: 991px) {
  .sidebar {
    height: 32em;
  }

  .sticky-top {
    position: relative !important;
  }
}
